
<template>
  <div class="hs-container">
    <c-header isBackNav :title="`${$t('base.TrainTicketsBooking')}-${Number(trainOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}`"></c-header>
    <hs-scroll
      class="table-view"
      :data="[details]"
      ref="tableView">
      <train-route @delegateForTableTime="_clickTableTime" @delegateNotice="_getNoticeRules" :route='trainOrder.GoSelectedRoute'></train-route>
      <train-route @delegateForTableTime="_clickTableTime" @delegateNotice="_getNoticeRules" :route='trainOrder.BackSelectedRoute'></train-route>
      <hs-section>
        <hs-cell-1 :showLogo="false">
          <div class="cell-passenger">
            <div class="cell-passenger-top" :type="language">
              <div>
                <p>{{$t('base.PassengerInformation')}}</p>
                <span>{{$t('base.OnlyAdultTicketBookingsAreSupported')}}</span>
              </div>
              <button @click="$refs.CPassenger.open()">{{$t('base.Add')}} + </button>
            </div>
          </div>
        </hs-cell-1>
        <hs-cell-1 v-for="(item, index) in trainOrder.Passengers" :key="`passengers${index}`">
          <div class="passenger-infor" :style="{transform: `translateX(${!trainOrder.Passengers[index].openDelete ? '0px' : '-65px'})`}">
            <div class="left" @click="_setPassengerOpenDelete(true, index)">
              <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
            </div>
            <div class="right" @click="_gotoPassengerDetails(index)">
              <div class="item">
                <p class="name" v-if="item.DefaultNameType === 0">{{item.ChName}}</p>
                <p class="name" v-else>{{item.LastName}}&nbsp;{{item.FirstName}}</p>
              </div>
              <div class="item" v-if="item.Credentials && item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0] && item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo">
                <p class="other-title">{{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialName}}</p>
                <p class="other-value">{{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo}}</p>
              </div>
              <div class="item" v-if="item.Mobile">
                <p class="other-title">{{$t('base.CellphoneNumber')}}</p>
                <p class="other-value">{{item.Mobile}}</p>
              </div>
              <div class="delete" @click.stop="_deletePassenger(index)">{{$t('base.Delete')}}</div>
            </div>
          </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false">
          <div class="cell-passenger-price">
            <p>{{$t('base.ServiceFee')}}&nbsp;￥{{serviceCharge}}{{$t('base.PreQuantifier')}}</p>
          </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" v-if="goSeatType>0&&passengerCount>0">
          <div class="cell-selection">
            <div class="cell-selection-top">
              <p>{{$t('base.Selection')}}<template v-if="backSeatType > 0"> ({{$t('base.Going')}})</template></p>
              <p>{{seatsSelectTypeGo.length}}/{{passengerCount}}</p>
            </div>
            <div class="cell-selection-bottom" :type="language">
              <p>{{$t('base.Window')}}</p>
              <button :style="{'background-image': _seatHaveSelect(1, 1, 'a') ? `url('static/image/train/seat_red_a.png')` : `url('static/image/train/seat_a.png')`}" @click="_seatSelection(1, 1, 'a')"></button>
              <button v-show="goSeatType!==3 && goSeatType!==4" :style="{'background-image': _seatHaveSelect(1, 1, 'b')? `url('static/image/train/seat_red_b.png')` : `url('static/image/train/seat_b.png')`}" @click="_seatSelection(1, 1, 'b')"></button>
              <button :style="{'background-image':_seatHaveSelect(1, 1, 'c') ? `url('static/image/train/seat_red_c.png')` : `url('static/image/train/seat_c.png')`}" @click="_seatSelection(1, 1, 'c')"></button>
              <p>{{$t('base.Aisle')}}</p>
              <button v-show="goSeatType!==3" :style="{'background-image': _seatHaveSelect(1, 1, 'd') ? `url('static/image/train/seat_red_d.png')` : `url('static/image/train/seat_d.png')`}" @click="_seatSelection(1, 1, 'd')"></button>
              <button  :style="{'background-image': _seatHaveSelect(1, 1, 'f') ? `url('static/image/train/seat_red_f.png')` : `url('static/image/train/seat_f.png')`}" @click="_seatSelection(1, 1, 'f')"></button>
              <p>{{$t('base.Window')}}</p>
            </div>
            <div class="cell-selection-bottom" v-if="passengerCount>1" :type="language">
              <p>{{$t('base.Window')}}</p>
              <button :style="{'background-image': _seatHaveSelect(1, 2, 'a') ? `url('static/image/train/seat_red_a.png')` : `url('static/image/train/seat_a.png')`}" @click="_seatSelection(1, 2, 'a')"></button>
              <button v-show="goSeatType!==3 && goSeatType!==4" :style="{'background-image': _seatHaveSelect(1, 2, 'b')? `url('static/image/train/seat_red_b.png')` : `url('static/image/train/seat_b.png')`}" @click="_seatSelection(1, 2, 'b')"></button>
              <button :style="{'background-image':_seatHaveSelect(1, 2, 'c') ? `url('static/image/train/seat_red_c.png')` : `url('static/image/train/seat_c.png')`}" @click="_seatSelection(1, 2, 'c')"></button>
              <p>{{$t('base.Aisle')}}</p>
              <button v-show="goSeatType!==3" :style="{'background-image': _seatHaveSelect(1, 2, 'd') ? `url('static/image/train/seat_red_d.png')` : `url('static/image/train/seat_d.png')`}" @click="_seatSelection(1, 2, 'd')"></button>
              <button :style="{'background-image': _seatHaveSelect(1, 2, 'f') ? `url('static/image/train/seat_red_f.png')` : `url('static/image/train/seat_f.png')`}" @click="_seatSelection(1, 2, 'f')"></button>
              <p>{{$t('base.Window')}}</p>
            </div>
          </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" v-if="backSeatType>0&&passengerCount>0">
          <div class="cell-selection">
            <div class="cell-selection-top">
              <p>{{$t('base.Selection')}} ({{$t('base.Return')}})</p>
             <p>{{seatsSelectTypeBack.length}}/{{passengerCount}}</p>
            </div>
            <div class="cell-selection-bottom" :type="language">
              <p>{{$t('base.Window')}}</p>
              <button :style="{'background-image': _seatHaveSelect(2, 1, 'a') ? `url('static/image/train/seat_red_a.png')` : `url('static/image/train/seat_a.png')`}" @click="_seatSelection(2, 1, 'a')"></button>
              <button v-show="goSeatType===3||goSeatType===4" :style="{'background-image': _seatHaveSelect(2, 1, 'b')? `url('static/image/train/seat_red_b.png')` : `url('static/image/train/seat_b.png')`}" @click="_seatSelection(2, 1, 'b')"></button>
              <button :style="{'background-image':_seatHaveSelect(2, 1, 'c') ? `url('static/image/train/seat_red_c.png')` : `url('static/image/train/seat_c.png')`}" @click="_seatSelection(2, 1, 'c')"></button>
              <p>{{$t('base.Aisle')}}</p>
              <button v-show="goSeatType===3" :style="{'background-image': _seatHaveSelect(2, 1, 'd') ? `url('static/image/train/seat_red_d.png')` : `url('static/image/train/seat_d.png')`}" @click="_seatSelection(2, 1, 'd')"></button>
              <button  :style="{'background-image': _seatHaveSelect(2, 1, 'f') ? `url('static/image/train/seat_red_f.png')` : `url('static/image/train/seat_f.png')`}" @click="_seatSelection(2, 1, 'f')"></button>
              <p>{{$t('base.Window')}}</p>
            </div>
            <div class="cell-selection-bottom" v-if="passengerCount>1" :type="language">
              <p>{{$t('base.Window')}}</p>
              <button :style="{'background-image': _seatHaveSelect(2, 2, 'a') ? `url('static/image/train/seat_red_a.png')` : `url('static/image/train/seat_a.png')`}" @click="_seatSelection(2, 2, 'a')"></button>
              <button v-show="goSeatType===3||goSeatType===4" :style="{'background-image': _seatHaveSelect(2, 2, 'b')? `url('static/image/train/seat_red_b.png')` : `url('static/image/train/seat_b.png')`}" @click="_seatSelection(2, 2, 'b')"></button>
              <button :style="{'background-image':_seatHaveSelect(2, 2, 'c') ? `url('static/image/train/seat_red_c.png')` : `url('static/image/train/seat_c.png')`}" @click="_seatSelection(2, 2, 'c')"></button>
              <p>{{$t('base.Aisle')}}</p>
              <button v-show="goSeatType===3" :style="{'background-image': _seatHaveSelect(2, 2, 'd') ? `url('static/image/train/seat_red_d.png')` : `url('static/image/train/seat_d.png')`}" @click="_seatSelection(2, 2, 'd')"></button>
              <button  :style="{'background-image': _seatHaveSelect(2, 2, 'f') ? `url('static/image/train/seat_red_f.png')` : `url('static/image/train/seat_f.png')`}" @click="_seatSelection(2, 2, 'f')"></button>
              <p>{{$t('base.Window')}}</p>
            </div>
          </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" @click.native="isAcptStanding=!isAcptStanding" v-if="isShowSeat">
          <div class="cell-passenger-seat">
            <div v-show="!isAcptStanding" class="logo" style="background-image: url('static/image/base/check-box/check-box-default.png')"></div>
            <div v-show="isAcptStanding" class="logo" style="background-image: url('static/image/base/check-box/check-box.png')"></div>
            <p>{{$t('base.WhetherToAcceptTheSeatlessTicket')}}</p>
          </div>
        </hs-cell-1>
      </hs-section>
      <hs-section>
        <hs-cell-small-title v-if="details.IsDisplayCustomItem" @click.native="showCustomItem = true"
          :title="details.CustomItemName"
          type="disabled-input"
          :value="trainOrder.CustomItem ? trainOrder.CustomItem.Name : ''"
          :placeholder="`(${details.IsRequiredCustomItem ? $t('base.RequiredChoose') : $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`">
        </hs-cell-small-title>
        <hs-cell-small-title v-if="details.IsDisplayPurpose"
          :title="$t('base.TravelPurpose')"
          type="input"
          v-model="purpose"
          :placeholder="`(${details.IsRequiredPurpose ? $t('base.RequiredFill') : $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
          :showLogo="false">
        </hs-cell-small-title>
        <hs-cell-small-title v-if="details.IsDisplayAuthorizationCode"
          :title="$t('base.AuthorizationCode')"
          type="input"
          v-model="authorizationCode"
          :placeholder="`(${details.IsRequiredAuthorizationCode ? $t('base.RequiredFill') : $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
          :showLogo="false">
        </hs-cell-small-title>
        <hs-cell-small-title v-if="details.VettingType === 3 && details.EnableVetting" @click.native="approversPicker.show()"
          :title="$t('base.Approver')"
          type="disabled-input"
          :value="trainOrder.AppointVettingPersonName"
          :placeholder="`(${$t('base.RequiredChoose')}) ${$t('base.PleaseChoose')}`">
        </hs-cell-small-title>
        <hs-cell-small-title @click.native="payTypePicker.show()"
          :title="$t('base.PaymentWay')"
          type="disabled-input"
          :value="trainOrder.PayType | filterPayType">
        </hs-cell-small-title>
      </hs-section>
      <hs-section>
        <!-- <hs-cell-1 value="联系人信息" logoName="add.png" @click.native="_addContact"></hs-cell-1> -->
        <hs-cell-1 :value="$t('base.ContactInformation')" logoName="add.png" @click.native="$refs.CFrequentContacts.open()"></hs-cell-1>
        <hs-cell-1 v-for="(item, index) in trainOrder.Contacts" :key="index">
          <div class="passenger-infor" :style="{transform: `translateX(${!trainOrder.Contacts[index].openDelete ? '0px' : '-65px'})`}">
            <div class="left" @click="_setContactsOpenDelete(true, index)">
              <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
            </div>
            <div class="right" @click="_gotoContactsDetails(index)">
              <div class="item">
                <p class="name">{{item.Name}}</p>
              </div>
              <div class="item">
                <p class="other-title">{{$t('base.CellphoneNumber')}}</p>
                <p class="other-value">{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
              </div>
              <div class="item">
                <p class="other-title">{{$t('base.Email')}}</p>
                <p class="other-value">{{item.Email ? item.Email : $t('base.Unfilled')}}</p>
              </div>
              <div class="delete" @click.stop="_deleteContact(index)">{{$t('base.Delete')}}</div>
            </div>
          </div>
        </hs-cell-1>
      </hs-section>
    </hs-scroll>
    <div class="footer" @click="showPriceDetail=!showPriceDetail">
      <div class="footer-price">
        <p>¥{{totalPrice}}</p>
        <div class="logo" :style="{transform: showPriceDetail ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
      <hs-gradient-button @click="_gotoConfirm"
        type="common"
        width="auto"
        :title="$t('base.NextStep')">
      </hs-gradient-button>
    </div>
    <hs-dialog v-model="showPriceDetail" height="calc(100% - 50px)" ref="dialog">
      <hs-scroll class="showPriceDetail">
        <hs-cell-1 :showLogo="false">
          <div class="footer-price-cell">
            <p>{{trainOrder.GoSelectedRoute?trainOrder.GoSelectedRoute.TypeName : $t('base.Price')}}<template v-if="details.BackChargeInfo">({{$t('base.Going')}})</template></p>
            <p><span>¥{{details.GoChargeInfo&&details.GoChargeInfo.SeatPrice}}</span><span> x {{trainOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
          </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" v-if="details.BackChargeInfo">
          <div class="footer-price-cell">
            <p>{{trainOrder.BackSelectedRoute?trainOrder.BackSelectedRoute.TypeName : $t('base.Price')}}({{$t('base.Return')}})</p>
            <p><span>¥{{details.BackChargeInfo&&details.BackChargeInfo.SeatPrice}}</span><span> x {{trainOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
          </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false">
          <div class="footer-price-cell">
            <p>{{$t('base.ServiceFee')}}</p>
            <p><span>￥{{serviceCharge}}</span><span> x {{trainOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
          </div>
        </hs-cell-1>
      </hs-scroll>
    </hs-dialog>
    <!-- 乘客 -->
    <c-passenger
      ref="CPassenger"
      :XOrder="trainOrder"
      :BookInit="details"
      :OrderBusinessType="10"
      @delegateWithConfirm="_delegateWithConfirm"
    ></c-passenger>
    <!-- 乘客详情 -->
    <passenger-details
      ref="PassengerDetails"
      :details="editingPassenger"
      :BookInit="details"
      :OrderBusinessType="10"
      @delegate="_delegateWithPassengerDetails"
    ></passenger-details>

    <c-contacts :OrderBusinessType="10" :show.sync="showContacts" :datalist="trainOrder.Contacts" @delegate="_delegateWithContacts" v-model="editingContact" ></c-contacts>
    <c-frequent-contacts ref="CFrequentContacts" :orderInit.sync="details" :datalist="trainOrder.Contacts" :OrderBusinessType="10" @delegate="_delegateWithFrequentContacts"></c-frequent-contacts>
    <custom-item :show.sync="showCustomItem" :orderInit.sync="details" @delegate="_delegateWithCustomItem"></custom-item>
    <violation-rank v-if="showViolationRank" v-model="showViolationRank" :details="violationRankDetails" @delegate="_nextStep"></violation-rank>
    <c-table-time v-if="showTableTime" :params="currentTableTime"></c-table-time>
    <hs-dialog v-model="showRulesDialog">
      <hs-scroll class="rulesDialog" :data="[rulesHtml]">
        <ul>
          <div v-html="rulesHtml"></div>
        </ul>
      </hs-scroll>
    </hs-dialog>
  </div>
</template>
<script type='text/ecmascript-6'>
import axios from 'axios'
import CContacts from 'components/c-passenger/c-contacts'
import CFrequentContacts from 'components/c-passenger/c-frequent-contacts'
import CHeader from 'components/c-header/c-header'
import CPassenger from 'components/c-passenger/c-passenger'
import CustomItem from 'components/c-passenger/c-custom-item'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsNull from 'base/hs-null/hs-null'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsTab from 'base/hs-tab/hs-tab'
import PassengerDetails from 'pages/personal/passengers/details.vue'
import TrainRoute from 'pages/train/book/train-route'
import Vue from 'vue'
import { mapGetters, mapMutations } from 'vuex'
import { kpiTrainGetBookInitInfo, kpiTrainCheckReasonCode, kpiTrainVerifyPassenger } from 'api'
const PAY_TYPE_PICKER_DATA_1 = { text: Vue.prototype.$t('base.CompanyPayment'), value: 1 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_2 = { text: Vue.prototype.$t('base.PersonalPayment'), value: 2 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_3 = { text: `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`, value: 3 } // eslint-disable-line
export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log(vm.details)
      if (vm.noValue(vm.trainOrder.TravelType)) {
        vm.$router.replace('/home')
      } else if (vm.noValue(JSON.stringify(vm.details))) {
        vm.details = {}
        vm._getDetails()
      }
    })
  },
  beforeRouteLeave(to, from, next) {
    if (this.$refs.CPassenger && this.$refs.CPassenger.show) {
      this.$refs.CPassenger.close()
      next(false)
    } else if (this.$refs.PassengerDetails && this.$refs.PassengerDetails.show) {
      this.$refs.PassengerDetails.close()
      next(false)
    } else if (to.path !== '/train/bookConfirm' && to.path !== '/train/query' && to.path !== '/home') {
      this._showTipDialog(
        this.$t('TM.YourOrderHasNotBeenFilledOut'),
        () => { next() },
        () => { next(false) }
      )
    } else {
      this.showContacts = false
      this.showPriceDetail = false
      this.showCustomItem = false
      this.showViolationRank = false
      this.showTableTime = false
      this.showRulesDialog = false
      next()
    }
  },
  computed: {
    ...mapGetters(['trainOrder']),
    // 差旅目的
    purpose: {
      get: function() {
        return this.trainOrder.Purpose
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.trainOrder))
        res.Purpose = val
        this.SET_TRAIN_ORDER(res)
      }
    },
    // 授权码
    authorizationCode: {
      get: function() {
        return this.trainOrder.AuthorizationCode
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.trainOrder))
        res.AuthorizationCode = val
        this.SET_TRAIN_ORDER(res)
      }
    },
    // 配送地址
    deliveryAddress: {
      get: function() {
        return this.trainOrder.DeliveryAddress
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.trainOrder))
        res.DeliveryAddress = val
        this.SET_TRAIN_ORDER(res)
      }
    },
    // 服务费
    serviceCharge() {
      let goServiceChange = this.details.GoChargeInfo && this.details.GoChargeInfo.ServiceCharge ? this.details.GoChargeInfo.ServiceCharge : 0
      let backServiceChange = this.details.BackChargeInfo && this.details.BackChargeInfo.ServiceCharge ? this.details.BackChargeInfo.ServiceCharge : 0
      return goServiceChange + backServiceChange
    },
    // 总价
    totalPrice() {
      let goSeatPrice = this.details.GoChargeInfo && this.details.GoChargeInfo.SeatPrice ? this.details.GoChargeInfo.SeatPrice : 0
      let backSeatPrice = this.details.BackChargeInfo && this.details.BackChargeInfo.SeatPrice ? this.details.BackChargeInfo.SeatPrice : 0
      return (goSeatPrice + backSeatPrice + this.serviceCharge) * this.trainOrder.Passengers.length
    },
    // 去程座位
    goSeatType() {
      if (this.trainOrder.GoSelectedRoute && !this.noValue(this.trainOrder.GoSelectedRoute.TrainCode)) {
        let seatType = this.trainOrder.GoSelectedRoute.TypeCode
        if (seatType === '1') { // 商务座
          return 3
        } else if (seatType === '3') { // 一等座
          return 4
        } else if (seatType === '4') { // 二等座
          return 5
        }
        return 0
      } else {
        return 0
      }
    },
    // 是否现在无座
    isShowSeat() {
      if (this.trainOrder.GoSelectedRoute && !this.noValue(this.trainOrder.GoSelectedRoute.TrainCode)) {
        let seatType = this.trainOrder.GoSelectedRoute.TypeCode
        if (seatType === '4' || seatType === '9') { // 4.二等座 9.硬座
          return true
        }
      }
      if (this.trainOrder.BackSelectedRoute && !this.noValue(this.trainOrder.BackSelectedRoute.TrainCode)) {
        let seatType = this.trainOrder.BackSelectedRoute.TypeCode
        if (seatType === '4' || seatType === '9') { // 4.二等座 9.硬座
          return true
        }
      }
      return false
    },
    // 回程座位
    backSeatType() {
      if (this.trainOrder.BackSelectedRoute && !this.noValue(this.trainOrder.BackSelectedRoute.TrainCode)) {
        let seatType = this.trainOrder.BackSelectedRoute.TypeCode
        if (seatType === '1') { // 商务座
          return 3
        } else if (seatType === '3') { // 一等座
          return 4
        } else if (seatType === '4') { // 二等座
          return 5
        }
        return 0
      } else {
        return 0
      }
    },
    // 乘客人数
    passengerCount() {
      return this.trainOrder.Passengers.length
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  data() {
    return {
      details: {},
      /** 联系人 */
      showContacts: false,
      editingContact: {},

      /** 价格明细 */
      showPriceDetail: false,

      /** 自定义项 */
      showCustomItem: false,

      /** 违反差旅标准 */
      showViolationRank: false,
      violationRankDetails: {},

      /** 时刻表 */
      showTableTime: false,
      currentTableTime: {},

      /** 预订须知 */
      showRulesDialog: false,
      rulesHtml: '',

      /** 常旅客 */
      editingPassenger: {},

      seatsSelectTypeGo: [], // 座位处理
      seatsSelectTypeBack: [], // 座位处理
      isAcptStanding: true
    }
  },
  methods: {
    ...mapMutations(['SET_TRAIN_ORDER']),
    // 获取配置信息
    _getDetails() {
      let params = {
        DepartStationCode: this.trainOrder.DepartStationCode,
        ArrivalStationCode: this.trainOrder.ArrivalStationCode,
        GoSelectedTrainInfo: {
          TrainCode: this.trainOrder.GoSelectedRoute.TrainCode,
          SeatTypeCode: this.trainOrder.GoSelectedRoute.TypeCode,
          DepartDate: this.trainOrder.GoSelectedRoute.DepartureDateTime,
          ChooseSeats: ''
        },
        BackSelectedTrainInfo: this.noValue(this.trainOrder.BackSelectedRoute.TrainCode) ? null : {
          TrainCode: this.trainOrder.BackSelectedRoute.TrainCode,
          SeatTypeCode: this.trainOrder.BackSelectedRoute.TypeCode,
          DepartDate: this.trainOrder.BackSelectedRoute.DepartureDateTime,
          ChooseSeats: ''
        },
        TravelType: this.trainOrder.TravelType
      }
      kpiTrainGetBookInitInfo(params).then(res => {
        console.log(res)
        this.details = res.ResultData
        // 默认联系人
        if (!this.noValue(JSON.stringify(this.details.DefaultContacts))) {
          let order = JSON.parse(JSON.stringify(this.trainOrder))
          order.Contacts = [Object.assign(this.details.DefaultContact, {openDelete: false})]
          // order.Contacts = this.details.DefaultContacts.map(v => {
          //   v.openDelete = false
          //   return v
          // })
          this.SET_TRAIN_ORDER(order)
        }
        // =====================================================
        this._creatPayTypePicker(this.details.PayTypes)
        this._creatApproversPicker(this.details.Approvers)
        // if (this.details.IsNeedDelivery) {
        //   this._createDeliveryTypePicker(this.details.DeliveryTypes, this.details.DefaultDeliveryAddress)
        // }
      })
    },
    // 初始化支付方式picker
    _creatPayTypePicker(PayTypes) {
      let data = []
      if (Number(this.trainOrder.TravelType) === 1) {
        data = [PAY_TYPE_PICKER_DATA_2]
      } else if (PayTypes) {
        PayTypes.forEach(element => {
          if (element === 1) {
            data.push(PAY_TYPE_PICKER_DATA_1)
          } else if (element === 2) {
            data.push(PAY_TYPE_PICKER_DATA_2)
          } else if (element === 3) {
            data.push(PAY_TYPE_PICKER_DATA_3)
          }
        })
      }
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.PayType = data[0] ? data[0].value : null
      this.SET_TRAIN_ORDER(res)
      this.payTypePicker = this.$createPicker({
        title: this.$t('base.PaymentWay'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: (val, index, text) => {
          let res = JSON.parse(JSON.stringify(this.trainOrder))
          res.PayType = val[0]
          this.SET_TRAIN_ORDER(res)
        }
      })
    },
    // 审批人picker
    _creatApproversPicker(Approvers) {
      let data = []
      if (Approvers) {
        Approvers.forEach(element => {
          data.push({
            text: element.Name,
            value: element.ID
          })
        })
      }
      this.approversPicker = this.$createPicker({
        title: this.$t('base.SelectApprover'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: (val, index, text) => {
          let res = JSON.parse(JSON.stringify(this.trainOrder))
          res.AppointVettingPersonID = val[0]
          res.AppointVettingPersonName = text[0]
          this.SET_TRAIN_ORDER(res)
        }
      })
    },
    _getNoticeRules() {
      let url = '/static/html/train_notice.html'
      axios.get(url).then((res) => {
        this.rulesHtml = res.data
        this.showRulesDialog = true
      })
    },
    /**
     * =================联系人处理======================
     */
    // 添加联系人
    // _addContact() {
    //   this.editingContact = this.trainOrder.Contacts.length > 0 ? {
    //     index: -1,
    //     Name: '',
    //     Mobile: '',
    //     Email: '',
    //     IsSendIssuedEmail: this.details.IsSendIssueEmailToContact,
    //     IsSendIssuedSms: this.details.IsSendIssueSmsToContact,
    //     openDelete: false
    //   } : Object.assign(this.details.DefaultContact, {openDelete: false, index: -1})
    //   this.showContacts = true
    // },
    // 保存联系人回调
    _delegateWithContacts(contacts) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Contacts.splice(contacts.index, 1, Object.assign(contacts, {openDelete: false}))
      this.SET_TRAIN_ORDER(res)
    },
    _setContactsOpenDelete(value, index) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Contacts[index].openDelete = value
      this.SET_TRAIN_ORDER(res)
    },
    _gotoContactsDetails(index) {
      if (this.trainOrder.Contacts[index].openDelete) {
        this._setContactsOpenDelete(false, index)
      } else {
        this.editingContact = Object.assign(JSON.parse(JSON.stringify(this.trainOrder.Contacts[index])), {index: index})
        this.showContacts = true
      }
    },
    _deleteContact(index) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Contacts.splice(index, 1)
      this.SET_TRAIN_ORDER(res)
    },
    _delegateWithFrequentContacts(val) {
      console.log('选中的联系人: ', val)
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Contacts = val
      this.SET_TRAIN_ORDER(res)
    },
    /**
     * =================乘客处理======================
     */
    // 修改乘机人回调
    _delegateWithPassengerDetails(passenger) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Passengers.splice(passenger.index, 1, passenger)
      this.SET_TRAIN_ORDER(res)
    },
    // 常旅客列表确定
    _delegateWithConfirm(datalist) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Passengers = datalist
      this.SET_TRAIN_ORDER(res)
    },
    _setPassengerOpenDelete(value, index) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Passengers[index].openDelete = value
      this.SET_TRAIN_ORDER(res)
    },
    _gotoPassengerDetails(index) {
      if (this.trainOrder.Passengers[index].openDelete) {
        this._setPassengerOpenDelete(false, index)
      } else {
        this.editingPassenger = Object.assign(JSON.parse(JSON.stringify(this.trainOrder.Passengers[index])), {index: index})
        this.$refs.PassengerDetails.open()
      }
    },
    // 删除乘客
    _deletePassenger(index) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.Passengers.splice(index, 1)
      this.SET_TRAIN_ORDER(res)
      this.seatsSelectTypeGo = [] // 座位处理
      this.seatsSelectTypeBack = [] // 座位处理
    },
    // 是否选择该座位
    _seatHaveSelect(routeType, row, seatNo) {
      if (routeType === 1) {
        return this.seatsSelectTypeGo.filter(item => item.row === row && item.seatNo === seatNo).length > 0
      }
      if (routeType === 2) {
        return this.seatsSelectTypeBack.filter(item => item.row === row && item.seatNo === seatNo).length > 0
      }
    },
    // 选座 routeType 1-去程 2-回程 row 1-第一排 2.第二排
    _seatSelection(routeType, row, seatNo) {
      if (this._seatHaveSelect(routeType, row, seatNo)) {
        return
      }
      if (routeType === 1) {
        if (this.seatsSelectTypeGo.length === this.passengerCount) {
          this.seatsSelectTypeGo.shift()
        }
        this.seatsSelectTypeGo.push({
          row: row,
          seatNo: seatNo
        })
      }
      if (routeType === 2) {
        if (this.seatsSelectTypeBack.length === this.passengerCount) {
          this.seatsSelectTypeBack.shift()
        }
        this.seatsSelectTypeBack.push({
          row: row,
          seatNo: seatNo
        })
      }
    },
    // 选择自定义项
    _delegateWithCustomItem(item) {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      res.CustomItem = item
      this.SET_TRAIN_ORDER(res)
    },
    // 确定
    _gotoConfirm() {
      console.log(this.trainOrder)
      if (this.noValue(JSON.stringify(this.details))) {
        this.$toast(this.$t('TM.TheSysteIsBusyPleaseReOperate'))
        this.$router.replace('/home')
        return
      }
      if ((this.seatsSelectTypeGo.length > 0 && this.seatsSelectTypeGo.length < this.trainOrder.Passengers.length) || (this.seatsSelectTypeBack.length > 0 && this.seatsSelectTypeBack.length < this.trainOrder.Passengers.length)) {
        this.$toast(this.$t('TM.YouAlsoHaveUnselectedPassengers'))
        return
      } else if (this.trainOrder.Passengers.length === 0) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.Passenger')))
        return
      } else if (this.details.IsRequiredCustomItem && this.noValue(JSON.stringify(this.trainOrder.CustomItem))) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.details.CustomItemName))
        return
      } else if (this.trainOrder.Contacts.length === 0) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Contacts')))
        return
      } else if (this.details.IsRequiredPurpose && this.noValue(this.trainOrder.Purpose)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.TravelPurpose')))
        return
      } else if (this.trainOrder.Purpose.length > 30) {
        this.$toast(this.$t('TM.LengthMustNotExceedXDigits', 'function', {name: this.$t('base.TravelPurpose'), number: 30}))
        return
      } else if (this.details.VettingType === 3 && this.details.EnableVetting && this.noValue(this.trainOrder.AppointVettingPersonName)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Approver')))
        return
      } else if (this.details.IsRequiredAuthorizationCode && this.noValue(this.trainOrder.AuthorizationCode)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.AuthorizationCode')))
        return
      } else if (this.trainOrder.AuthorizationCode.length > 20) {
        this.$toast(this.$t('TM.LengthMustNotExceedXDigits', 'function', {name: this.$t('base.AuthorizationCode'), number: 20}))
        return
      } else if (this.trainOrder.PayType !== 1 && this.trainOrder.PayType !== 2 & this.trainOrder.PayType !== 3) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.PaymentWay')))
        return
      } else if (this.details.IsNeedDelivery && this.trainOrder.DeliveryType !== 2 && this.trainOrder.DeliveryType !== 3 && this.noValue(this.trainOrder.DeliveryAddress)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.DeliveryAddress')))
        return
      } else {
        for (let item of this.trainOrder.Passengers) {
          if (this.noValue(item.Mobile)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.CellphoneNumber')}))
            return
          } else if (this.details.IsRequiredCostCenter && this.noValue(JSON.stringify(item.CostCenter))) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.CostCenter')}))
            return
          } else if (this.details.IsRequiedDepartment && this.noValue(JSON.stringify(item.Department))) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.Department')}))
            return
          } else if (this.details.IsRequiredBusinessUnit && this.noValue(JSON.stringify(item.BusinessUnit))) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.details.BusinessUnitName}))
            return
          } else if (this.noValue(JSON.stringify(item.Credential)) || this.noValue(item.Credential.CredentialNo)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.ID')}))
            return
          }
        }
        for (let item of this.trainOrder.Contacts) {
          if (this.noValue(item.Mobile)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.CellphoneNumber')}))
            return
          } else if (this.noValue(item.Email) && (item.IsSendBookEmail || item.IsSendIssuedEmail || item.IsSendConfirmEmail)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.Email')}))
            return
          }
        }
        let maplist = new Map()
        if (this.trainOrder.Contacts.filter(a => !maplist.has(a.Mobile) && maplist.set(a.Mobile, 1)).length < this.trainOrder.Contacts.length) {
          this.$toast(this.$t('TM.ThereMustBeNoContactWithTheSamePhoneNumber'))
          return
        }
      }
      /** 填写姓名 */
      let trainOrder = JSON.parse(JSON.stringify(this.trainOrder))
      trainOrder.Passengers.forEach((element, index) => {
        trainOrder.Passengers[index].Name = element.DefaultNameType === 0 ? element.ChName : `${element.LastName} ${element.FirstName}`
      })
      this.SET_TRAIN_ORDER(trainOrder)

      let passengers = this.trainOrder.Passengers.map(item => {
        return {
          Name: item.Name,
          Credential: item.Credential
        }
      })
      kpiTrainVerifyPassenger({Passengers: passengers}).then(res => {
        console.log('检查乘客: ', res)
        if (res.ResultData.IsSuccess) {
          if (Number(this.trainOrder.TravelType) === 0) {
            this._checkReason()
          } else {
            this._nextStep()
          }
        } else {
          this.$toast(this.$t('TM.XIDIsWrong', 'function', res.ResultData.Passengers.join(',')))
        }
      })
    },
    // 检查ReasonCode
    _checkReason() {
      let params = {
        DepartStationCode: this.trainOrder.DepartStationCode,
        ArrivalStationCode: this.trainOrder.ArrivalStationCode,
        GoSelectedTrainInfo: {
          TrainCode: this.trainOrder.GoSelectedRoute.TrainCode,
          SeatTypeCode: this.trainOrder.GoSelectedRoute.TypeCode,
          DepartDate: this.trainOrder.GoSelectedRoute.DepartureDateTime,
          ChooseSeats: ''
        },
        BackSelectedTrainInfo: this.noValue(this.trainOrder.BackSelectedRoute.TrainCode) ? null : {
          TrainCode: this.trainOrder.BackSelectedRoute.TrainCode,
          SeatTypeCode: this.trainOrder.BackSelectedRoute.TypeCode,
          DepartDate: this.trainOrder.BackSelectedRoute.DepartureDateTime,
          ChooseSeats: ''
        },
        CheckPassengers: this.trainOrder.Passengers,
        CustomItem: this.noValue(JSON.stringify(this.trainOrder.CustomItem)) ? null : this.trainOrder.CustomItem,
        AppointVettingPersonID: this.noValue(this.trainOrder.AppointVettingPersonID) ? null : this.trainOrder.AppointVettingPersonID,
        AppointVettingPersonName: this.noValue(this.trainOrder.AppointVettingPersonName) ? null : this.trainOrder.AppointVettingPersonName,
        PayType: this.trainOrder.PayType
      }
      kpiTrainCheckReasonCode(params).then(res => {
        console.log('审核: ', res)
        if (res.ResultData.IsNeedVetting && !this.noValue(res.ResultData.VettingProcessList)) {
          let order = JSON.parse(JSON.stringify(this.trainOrder))
          order.VettingProcessList = res.ResultData.VettingProcessList
          this.SET_TRAIN_ORDER(order)
        }
        if (res.ResultData.IsViolate) {
          if (res.ResultData.ViolateRankResult === 1) {
            this.violationRankDetails = res.ResultData
            this.showViolationRank = true
          } else if (res.ResultData.ViolateRankResult === 2) {
            const {GoPassengers, BackPassengers} = res.ResultData
            let goLimit, backLimit, limtInfo
            if (!this.noValue(JSON.stringify(GoPassengers))) {
              // goLimit = GoPassengers.map(item => item.Name).join(',') + '违反差旅政策,' + GoPassengers.filter(item => item.ViolateRankResult === 2).map(item => item.Name).join(',') + '不可预定'
              // FIXME 这里面有问题, 万一后面没有值, 看起来就会有点奇怪
              goLimit = this.$t('TM.XViolateAndXCannotBook', 'function', {violatedName: GoPassengers.map(item => item.Name).join(','), BookingName: GoPassengers.filter(item => item.ViolateRankResult === 2).map(item => item.Name).join(',')})
            }
            if (!this.noValue(JSON.stringify(BackPassengers))) {
              // backLimit = BackPassengers.map(item => item.Name).join(',') + '违反差旅政策,' + BackPassengers.filter(item => item.ViolateRankResult === 2).map(item => item.Name).join(',') + '不可预定'
              // FIXME 这里面有问题, 万一后面没有值, 看起来就会有点奇怪
              backLimit = this.$t('TM.XViolateAndXCannotBook', 'function', {violatedName: BackPassengers.map(item => item.Name).join(','), BookingName: BackPassengers.filter(item => item.ViolateRankResult === 2).map(item => item.Name).join(',')})
            }
            if (goLimit && backLimit) {
              limtInfo = this.$t('base.Going') + goLimit + '\n' + this.$t('base.Return') + backLimit
            } else if (backLimit) {
              limtInfo = this.$t('base.Return') + backLimit
            } else {
              limtInfo = goLimit
            }
            this.$toast(`${limtInfo}`)
          }
        } else {
          let order = JSON.parse(JSON.stringify(this.trainOrder))
          order.ViolationRankName = ''
          order.ViolationRankReason = ''
          this.SET_TRAIN_ORDER(order)
          this._nextStep()
        }
      })
    },
    // 下一步
    _nextStep() {
      let res = JSON.parse(JSON.stringify(this.trainOrder))
      let goChooseSeats = ''
      for (let seat of this.seatsSelectTypeGo) {
        goChooseSeats += seat.row + seat.seatNo
      }
      res.GoSelectedRoute.ChooseSeats = goChooseSeats
      let backChooseSeats = ''
      for (let seat of this.seatsSelectTypeBack) {
        backChooseSeats += seat.row + seat.seatNo
      }
      res.BackSelectedRoute.ChooseSeats = backChooseSeats
      res.BackSelectedRoute.IsAcptStanding = this.isAcptStanding
      this.SET_TRAIN_ORDER(res)
      this.$router.push({
        name: 'trainBookConfirm',
        params: {
          details: this.details
        }
      })
    },
    // 当前点击时刻表
    _clickTableTime(item) {
      this.currentTableTime = {
        DepartStationCode: item.DepartStationCode,
        DepartStationName: item.DepartStationName,
        ArrivalStationCode: item.ArrivalStationCode,
        ArrivalStationName: item.ArrivalStationName,
        DepartDate: item.DepartureDateTime,
        TrainNo: item.TrainNo,
        TrainCode: item.TrainCode
      }
      this.showTableTime = true
    },
    _showTipDialog(tipContent, next, cancel, confirmBtn = this.$t('base.Confirm')) {
      this.dialog = this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: this.$t('base.Tips'),
        content: tipContent,
        confirmBtn: {
          text: confirmBtn,
          active: true,
          disabled: false,
          href: 'javascript:'
        },
        cancelBtn: {
          text: this.$t('base.Cancel'),
          active: false,
          disabled: false,
          href: 'javascript:'
        },
        onConfirm: next,
        onCancel: cancel
      })
      this.dialog.show()
    }
  },
  components: {
    CContacts,
    CFrequentContacts,
    CHeader,
    CPassenger,
    CTableTime: () => import('components/c-table-time/c-table-time'),
    CustomItem,
    HsButton,
    HsCell1,
    HsCellSmallTitle,
    HsDialog,
    HsGradientButton,
    HsNull,
    HsScroll,
    HsSection,
    HsTab,
    PassengerDetails,
    TrainRoute,
    ViolationRank: () => import('./violation-rank')
  }
}
</script>
