      >.cell-passenger-top[type="EN"]
        padding 10px 0
        >div
          @extend .flex-column
          @extend .flex-top
          >p
            margin-bottom 5px
          >span
            margin-left 0
      >.cell-content-top
        @extend .flex-row
        @extend .flex-between
        >p
          &:nth-child(1)
