        >button
          &:nth-child(2)
            font-size 1.2rem
            background-color $color_white
            color $color_theme
    .passenger-infor
      @extend .flex-row
      @extend .flex-start
      @extend .no-wrap
      width 100%
      padding 10px 0
      transition all 400ms
      >.left
        @extend .flex-center
        width 60px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      >.right
        width calc(100% - 60px)
        position relative
        >.item
          @extend .flex-row
          @extend .flex-between
          @extend .flex-top
          width 100%
          margin 5px 0
          >.name
            color $color_black_text_0
            font-weight bold
            line-height 1.4em
            width: 100%
            margin-bottom 3px
          >.other-title
            line-height 1.1em
            color $color_gray_text
            font-size 1rem
            width 60px
          >.other-value
            line-height 1.1em
