            font-size 1rem
            width calc(100% - 70px)
            word-break break-all
            white-space normal
        >.delete
          @extend .flex-center
          position absolute
          width 60px
          // height 100%
          background-color $color_theme
          color $color_white
          font-size 0.9rem
          top -11px
          right -100px
          bottom -11px
      &:last-child
